<template>
    <div class="section others">
        <v-container>
            <v-layout row wrap>
                <v-flex xs12 md3 v-for="(app, index) in data" :key="index">
                    <a href="javascript:void(0)" @click="open(app.id)">
                        <app-list>
                            <template slot="appicon">
                                <img v-if="app.icon" :src="require('@/assets/app/icon/' + app.icon)" :alt="app.name">
                            </template>

                            <template slot="appname">
                                {{ app.name }}
                            </template>
                            <p slot="description">{{ app.description }}</p>
                        </app-list>
                    </a>
                </v-flex>
            </v-layout>
        </v-container>
    </div>
</template>

<script>
    import List from './_List.vue';
    import { eventBus } from '../main';
    
    export default {
        name: 'others',
        components: {
            'appList': List,
        },
        props: [ 'data' ],
        data() {
            return {
            }
        },
        methods: {
            open(id) {
                this.$emit('open');
                eventBus.$emit('currentChecked', id);
            }
        }
    }
</script>