<template>
    <div v-if="data" class="section infotracker">
        <v-container fill-height>
            <v-layout row wrap align-center>
                <v-flex xs12 sm12 md7>
                    <div class="app__screen">
                        <img src="../assets/app/image/InfoTracker-appimg-01.jpg" alt="InfoTracker" 
                            class="app__screen-01" 
                            data-aos="zoom-in"
                            data-depth='0.05' 
                            data-type='parallax'>
                        <img src="../assets/app/image/InfoTracker-appimg-02.jpg" alt="InfoTracker" 
                            class="app__screen-02" 
                            data-aos="zoom-in" 
                            data-aos-delay="100"
                            data-depth='0.1' 
                            data-type='parallax'>
                        <img src="../assets/app/image/InfoTracker-appimg-03.jpg" alt="InfoTracker" 
                            class="app__screen-03" 
                            data-aos="zoom-in" 
                            data-aos-delay="200"
                            data-depth='0.1' 
                            data-type='parallax'>
                        <img src="../assets/app/image/InfoTracker-appimg-04.jpg" alt="InfoTracker" 
                            class="app__screen-04" 
                            data-aos="fade-right" 
                            data-aos-delay="300">
                        <img src="../assets/app/image/InfoTracker-appimg-05.jpg" alt="InfoTracker" 
                            class="app__screen-05" 
                            data-aos="fade-left" 
                            data-aos-delay="350">
                    </div>
                </v-flex>
                <v-flex xs12 sm12 md5>
                    <v-layout align-start justify-end class="app__info">
                        <app-card>
                            <template slot="appicon">
                                <img :src="require('@/assets/app/icon/' + data.icon)" alt="InfoTracker">
                            </template>

                            <template slot="appname">
                                {{ data.name }}
                            </template>

                            <template slot="title">
                                {{ data.title }}
                            </template>
                            <p slot="description">{{ data.description }}</p>

                            <v-layout justify-end slot="appbtn">
                                <button @click="open(data.id)" class="btn download">More <v-icon right dark>trending_flat</v-icon></button>
                            </v-layout>
                        </app-card>
                    </v-layout>
                </v-flex>
            </v-layout>
        </v-container>
    </div>
</template>

<script>
    import Card from './_Card.vue';
    import {
        eventBus
    } from '../main';
    
    export default {
        name: 'infotracker',
        components: {
            'appCard': Card,
        },
        props: ['data'],
        data() {
            return {
            }
        },
        methods: {
            handleScroll() {                
                var depth, i, layer, layers, len, movement, topDistance, translate3d;

                topDistance = window.pageYOffset;

                layers = document.querySelectorAll("[data-type='parallax']");

                for (i = 0, len = layers.length; i < len; i++) {
                    layer = layers[i];
                    depth = layer.getAttribute('data-depth');
                    movement = -(topDistance * depth);

                    translate3d = 'translate3d(0, ' + movement + 'px, 0)';
                    layer.style['-webkit-transform'] = translate3d;
                    layer.style['-moz-transform'] = translate3d;
                    layer.style['-ms-transform'] = translate3d;
                    layer.style['-o-transform'] = translate3d;
                    layer.style.transform = translate3d;
                }
            },
            open(id) {
                this.$emit('open');
                eventBus.$emit('currentChecked', id);
            }
        },
        created() {
            window.addEventListener('scroll', this.handleScroll);
        },
        destroyed() {
            window.removeEventListener('scroll', this.handleScroll);
        }
    }
</script>

<style>
.box {
  border: 1px solid rgba(255, 255, 255, 0.5);
  padding: 8px 20px;
  line-height: 1.3em;
  opacity: 0;
  color: white;
  width: 200px;
  margin: 0 auto;
  margin-top: 30px;
  transform: translateZ(0);
  perspective: 1000px;
  backface-visibility: hidden;
  background: rgba(255, 255, 255, 0.1);
  transition: 1.5s all cubic-bezier(0.39, 0.575, 0.565, 1);
}
</style>
