<template>
    <div v-if="data" class="section genie">
        <v-layout>
            <v-container>
                <div class="app__screen">
                    <img src="../assets/app/image/Genie-appimg-01.png" alt="Genie" 
                    class="app__screen-01" 
                    data-aos="fade-up"
                    data-depth='0.05' 
                    data-type='parallax'>

                    <img src="../assets/app/image/Genie-appimg-02.png" alt="Genie" 
                    class="app__screen-02" 
                    data-aos="fade"
                    data-depth='0.15' 
                    data-type='parallax'>
                </div>
                <v-layout align-center justify-end class="app__info">
                    <app-card>
                        <template slot="appicon">
                            <img :src="require('@/assets/app/icon/' + data.icon)" alt="Genie">
                        </template>
                        <template slot="appname">{{ data.name }}</template>
                        <template slot="title">{{ data.title }}</template>
                        <p slot="description">{{ data.description }}</p>

                        <v-layout justify-end slot="appbtn">
                            <button @click="open(data.id)" class="btn download">More <v-icon right dark>trending_flat</v-icon></button>
                        </v-layout>
                    </app-card>
                </v-layout>
            </v-container>
        </v-layout>
    </div>
</template>

<script>
    import Card from './_Card.vue';
    import { eventBus } from '../main';

    export default {
        name: 'genie',
        components: {
            'appCard': Card,
        },
        props: [ 'data' ],
        data() {
            return {
            }
        },
        methods: {
            handleScroll() {                
                var depth, i, layer, layers, len, movement, topDistance, translate3d;

                topDistance = window.pageYOffset;

                layers = document.querySelectorAll("[data-type='parallax']");
                for (i = 0, len = layers.length; i < len; i++) {
                    layer = layers[i];
                    depth = layer.getAttribute('data-depth');
                    movement = -(topDistance * depth);
                    translate3d = 'translate3d(0, ' + movement + 'px, 0)';
                    layer.style['-webkit-transform'] = translate3d;
                    layer.style['-moz-transform'] = translate3d;
                    layer.style['-ms-transform'] = translate3d;
                    layer.style['-o-transform'] = translate3d;
                    layer.style.transform = translate3d;
                }
            },
            open(id) {
                this.$emit('open');
                eventBus.$emit('currentChecked', id);
            }
        },
        created() {
            window.addEventListener('scroll', this.handleScroll);
        },
        destroyed() {
            window.removeEventListener('scroll', this.handleScroll);
        }
    }
</script>