                </v-flex>
    
                <v-flex sm12 md6 v-show="appinfo.screenUrl != ''">

                    <carousel v-if="appinfo.screenType == 'pad'" class="details__img pad" 
                        :autoplay="true"
                        :autoplayTimeout="3000"
