<script>
import apps from '@/assets/data/apps.json';
import { eventBus } from '../main';
import { Carousel, Slide } from 'vue-carousel';

    export default {
        components: {
            Carousel,
            Slide
        },
        data() {
            return {
                appinfo: [],
                loader: null,
                loading: false,
                loading1: false,
                iOSPath: 'itms-services://?action=download-manifest&url=' + window.location.href
            }
        },
        methods: {
            close() {
                this.$emit('close');
            },
            downloadAPP() {
                this.loader = 'loading'
            }
        },
        watch: {
            loader() {
                const l = this.loader
                this[l] = !this[l]
    
                setTimeout(() => (this[l] = false), 2000)
    
                this.loader = null
            }
        },
        created() {
            eventBus.$on('currentChecked', (id) => {
                for (let key in apps) {
                    if (apps[key].id == id) {
                        this.appinfo = apps[key];
                    }
                }
            });
        }
    }
</script>

<style>
    .v-btn {
        text-transform: initial;
    }
    .custom-loader {
        animation: loader 1s infinite;
        display: flex;
    }
    
    @-moz-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    
    @-webkit-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    
    @-o-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    
    @keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
</style>
