<template>
  <div id="Home">
    <hero />
    <div class="bg__shapes shape-1"></div>
    <div class="bg__shapes shape-1-s"></div>
    <div class="bg__shapes diamond-1"></div>
    <div class="bg__shapes diamond-2"></div>
    <div class="bg__shapes diamond-3"></div>
    <app-genie :data="genieData" @open="openDialog"></app-genie>
    <app-infotracker :data="infoTrackerData" @open="openDialog"></app-infotracker>
    <app-infochatting :data="infoChattingData" @open="openDialog"></app-infochatting>
    <app-others :data="othersData" @open="openDialog"></app-others>
    <transition name="slide-fade">
      <app-details v-show="isVisible" @close="isVisible = false"></app-details>
    </transition>
  </div>
</template>

<script>
  import apps from '@/assets/data/apps.json';
  import hero from '../components/_Hero';
  import Genie from '../components/Genie';
  import InfoTracker from '../components/InfoTracker';
  import InfoChatting from '../components/InfoChatting';
  import Others from '../components/Others';
  import Details from '../components/_Details';
  import { eventBus } from '../main';

  
  export default {
    name: 'Home',
    data() {
      return {
        genieData: '',
        infoTrackerData: '',
        infoChattingData: '',
        othersData: [],
        isVisible: false
      }
    },
    components: {
      hero,
      'appGenie': Genie,
      'appInfotracker': InfoTracker,
      'appInfochatting': InfoChatting,
      'appOthers': Others,
      'appDetails': Details
    },
    created() {
      for (let key in apps) {
        switch(apps[key].name) {
          case "Genie":
            this.genieData = apps[key];
            break;
          case "InfoTracker":
            this.infoTrackerData = apps[key];
            break;
          case "InfoChatting":
            this.infoChattingData = apps[key];
            break;
          default:
            this.othersData.push(apps[key]);
        }
      }
    },
    methods: {
      openDialog() {
        this.isVisible = true;
      }
    }
  }
</script>


